	module.exports = "<h2 id=overview>Overview</h2> <p>A stack layout is a layout that positions the children vertically. In a Stack layout there is one primary child, and one or more secondary children. The primary child will expand vertically to fill the container if needed.</p> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Usage</th> </tr> </thead> <tbody> <tr> <td><code>.pf-l-stack</code></td> <td><strong>Outcome:</strong> Initiates the stack layout</td> <td><strong>Required:</strong> Yes</td> </tr> <tr> <td><code>.pf-l-stack__primary</code></td> <td><strong>Outcome:</strong> Specifies the primary child of the layout, which expands vertically when needed.</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-l-stack__secondary</code></td> <td><strong>Outcome:</strong> Specifies a secondary child of the layout, which accommodates its content.</td> <td><strong>Required:</strong> No</td> <td><strong>Remarks:</strong> A stack can have one or more secondary children</td> </tr> <tr> <td><code>.pf-has-gutter</code></td> <td><strong>Outcome:</strong> Adds space between children</td> <td><strong>Required:</strong> No</td> </tr> </tbody> </table> ";

